.catalog {
  display: flex;
  justify-content: center;

  &-center {
    display: flex;
    flex-wrap: wrap;
    padding-top: 50px;
    padding-bottom: 50px;
    justify-content: center;
    align-items: center;
    flex-grow: 1;
    flex-shrink: 0;
    max-width: 1025px;
    min-width: 1024px;
  }

  & :nth-child(4n) {
    margin-right: 0;
  }
}
